.card {
  background: red;
  border-radius: 12px;
  color: aqua;
}

html,
body {
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-align: center;
}

.sg-colors {
  background: #cacaca;
  text-align: center;
  padding: 30px 20%;
}
.sg-colors h4 {
  margin: 0 0 10px 0;
  padding: 0;
}

.color-palette {
  display: flex;
  justify-content: space-evenly;
}

.color-swatch {
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 5px 0 #000;
  background: #fff;
  padding: 10px;
  border: 1px solid #000;
}

.swatch-name::before {
  content: "";
  height: 80px;
  width: 130px;
  display: block;
  background: lime;
  border: 1px solid #000;
}

.sec-swatch .swatch-name::before {
  background: #9d00ff;
}

.typography {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.typography h2 {
  width: 100%;
}
.typography .font-sampler {
  text-align: center;
}

#text-content {
  display: flex;
  flex-wrap: wrap;
  padding: 30px 10%;
  justify-content: center;
  gap: 30px;
}
#text-content div {
  background: lightgreen;
}
#text-content #headings {
  width: 200px;
}
#text-content #common-text-elements {
  width: 800px;
  display: flex;
}
#text-content #common-text-elements div:first-child {
  width: 30%;
}
#text-content #common-text-elements div:last-child {
  width: 70%;
}
#text-content #misc-text-elements {
  width: 400px;
}
#text-content #in-out-elements {
  display: none;
}
#text-content #paragraphs {
  width: calc(50% - 15px);
}
#text-content #lists {
  display: flex;
  width: calc(50% - 15px);
}

